import React from "react";
import { useStaticQuery, graphql } from "gatsby";
import Fade from "react-reveal/Fade";

import Container from "common/components/UI/ContainerTwo";
import Heading from "common/components/Heading";
import Image from "common/components/Image";
import Link from "common/components/Link";
import Text from "common/components/Text";
import logo from "common/assets/image/logo.png";

import FooterWrapper, {
  FooterInner,
  CopyrightInfo,
  FooterWidget,
  Nav,
} from "./footer.style";

const Footer = () => {
  const Data = useStaticQuery(graphql`
    query {
      Logo: file(relativePath: { eq: "image/agencyModern/logo.png" }) {
        childImageSharp {
          fluid(quality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      agencyModernJson {
        aboutUs {
          id
          title
        }
        ourInformation {
          id
          title
        }
        social {
          id
          title
          href
          icon {
            childImageSharp {
              fluid(quality: 100) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  `);

  return (
    <FooterWrapper>
      <Container>
        <FooterInner>
          <CopyrightInfo>
            <Fade up delay={100}>
              <Image src={logo} alt="Logo" />
              <Text
                className="copyright"
                content="Copyright by Exsite Studio"
              />
            </Fade>
          </CopyrightInfo>

          <FooterWidget>
            <Fade up delay={200}>
              <Heading as="h4" content="About Exsite" />
              <Nav>
                {Data.agencyModernJson.aboutUs.map((item) => (
                  <Link key={item.id} href="#">
                    {item.title}
                  </Link>
                ))}
              </Nav>
            </Fade>
          </FooterWidget>

          <FooterWidget>
            <Fade up delay={300}>
              <Heading as="h4" content="Website Information" />
              <Nav>
                {Data.agencyModernJson.ourInformation.map((item) => (
                  <Link key={item.id} href="#">
                    {item.title}
                  </Link>
                ))}
              </Nav>
            </Fade>
          </FooterWidget>

          {/* <FooterWidget>
            <Fade up delay={400}>
              <Heading as="h4" content="My Account" />
              <Nav>
                {Data.agencyModernJson.myAccount.map((item) => (
                  <Link key={item.id} href="#">
                    {item.title}
                  </Link>
                ))}
              </Nav>
            </Fade>
          </FooterWidget> */}

          <FooterWidget>
            <Fade up delay={500}>
              <Heading as="h4" content="Social Media" />
              <Nav>
                {Data.agencyModernJson.social.map((item) => (
                  <Link key={item.id} href={item.href} target="_blank">
                    <Image
                      src={item.icon.childImageSharp.fluid.src}
                      alt={item.title}
                      width={20}
                    />
                    {item.title}
                  </Link>
                ))}
              </Nav>
            </Fade>
          </FooterWidget>
        </FooterInner>
      </Container>
    </FooterWrapper>
  );
};

export default Footer;
